import { createWebHistory, createRouter } from "vue-router";
import Home from './components/Home'
import TheList from './components/TheList'
import TheDetail from './components/TheDetail'
import TheAuthor from './components/TheAuthor'
import TheComment from './components/TheComment'

const routes = [
  {
    path: "/list",
    component: TheList,
  },
  {
    path: "/",
    component: Home,    
  },
  {
    path: "/detail/:id",
    component: TheDetail,
    children:[
      {
        path:"author",
        component:TheAuthor,
      },
      {
        path:"comment",
        component:TheComment,
      }
    ]    
  },
  {
    path:"/:anything(.*)",
    component:Home,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 